import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { Tileset } from './tileset';

var scene = new THREE.Scene();
scene.background = new THREE.Color( 0x283831 );
const light = new THREE.AmbientLight( 0xeeeeee, 1 ); // soft white light
scene.add( light );

// build camera
var camera = new THREE.PerspectiveCamera( 75, window.innerWidth / window.innerHeight, 1, 10000 );
camera.position.x = 2500;
camera.position.y = 2500;
camera.position.z = 3000;

var renderer = new THREE.WebGLRenderer();
renderer.setSize( window.innerWidth, window.innerHeight );

document.body.appendChild( renderer.domElement );


// controls
var controls = new OrbitControls( camera, renderer.domElement );
//controls.maxPolarAngle = Math.PI * 0.5;
controls.minDistance = 1;
controls.maxDistance = 5000;
controls.target.y = 1000;

// handle resize
window.addEventListener( 'resize', onWindowResize, false );
function onWindowResize() {

    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();

    renderer.setSize( window.innerWidth, window.innerHeight );

}

function animate() {
  requestAnimationFrame( animate );
  camera.updateMatrixWorld();
  renderer.render( scene, camera );
}
animate();

var tileset = new Tileset("aya/tileset.json", scene, camera, 1.0);
tileset.setLoadAroundView(true);

setInterval(function(){
  tileset.update();
}, 500);


